import React from 'react';
import { Pie } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

ChartJS.register(ArcElement, Tooltip, Legend);

const TaskVisualization = ({ totalTasks, completedTasks, remainingTasks }) => {
  const data = {
    labels: ['Completed', 'Remaining', 'Total Added'],
    datasets: [
      {
        data: [completedTasks, remainingTasks, totalTasks],
        backgroundColor: ['#34c759', '#ea4335', '#4285f4'],
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'bottom',
      },
      title: {
        display: true,
        text: 'Task Progress Visualization',
      },
    },
  };

  return (
    <div className="task-visualization">
      <Pie data={data} options={options} />
    </div>
  );
};

export default TaskVisualization;