import React, { useState, useEffect } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import TaskCreator from './components/TaskCreator';
import TaskBar from './components/TaskBar';
import DailyTasks from './components/DailyTasks';
import TaskStats from './components/TaskStats';
import TaskVisualization from './components/TaskVisualization';
import { db } from './firebase/firebaseConfig';
import { collection, onSnapshot } from 'firebase/firestore';
import './styles/App.css';

function App() {
  const [availableTasks, setAvailableTasks] = useState([]);
  const [dailyTasks, setDailyTasks] = useState([]);

  useEffect(() => {
    const unsubscribe = onSnapshot(collection(db, 'tasks'), (snapshot) => {
      const tasksData = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setAvailableTasks(tasksData); // Removed filtering
    });

    return () => unsubscribe();
  }, []); // Removed dailyTasks dependency

  const totalTasks = availableTasks.length;
  const completedTasks = dailyTasks.filter(t => t.completed).length;
  const remainingTasks = dailyTasks.filter(t => !t.completed).length;

  return (
    <DndProvider backend={HTML5Backend}>
      <div className="app-wrapper">
        <nav className="navbar">
          <h1>Task Bar</h1>
        </nav>
        <div className="app-container">
          <div className="left-sidebar">
            <TaskCreator />
            <div className="tasks-list">
              {availableTasks.map((task) => (
                <TaskBar 
                  key={task.id} 
                  task={task} 
                  isInDaily={dailyTasks.some(dt => dt.id === task.id)}
                />
              ))}
            </div>
          </div>
          <div className="main-content">
            <DailyTasks tasks={dailyTasks} setTasks={setDailyTasks} />
            <TaskStats tasks={dailyTasks} />
            <TaskVisualization 
              totalTasks={totalTasks} 
              completedTasks={completedTasks} 
              remainingTasks={remainingTasks} 
            />
          </div>
        </div>
      </div>
    </DndProvider>
  );
}

export default App;